.bgSelectorContainer {
  width: 80%;
  max-width: 400px;
  margin: auto;
  background: rgba(0, 0, 0, 0.4);
  padding: 16px;
  border-radius: 16px;
  margin-bottom: 16px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap; 
  overflow: auto;
}

.bgSelectorThumbnail {
  width: 100%;
  height: 100%;
}

.bgSelectorItem {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  margin: 0px 8px;
  width: 100px;
  height: 100px;
  border-radius: 8px;
  overflow: hidden;
  -webkit-box-shadow: -3px 3px 3px rgba(0,0,0,.3);
          box-shadow: -3px 3px 3px rgba(0,0,0,.3);
  cursor: pointer;
  -webkit-transition: all 300ms ease-in-out;
  -o-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.bgSelectorItem:hover {
  -webkit-box-shadow: -5px 5px 3px rgba(0, 0,0, .55);
          box-shadow: -5px 5px 3px rgba(0, 0,0, .55);
}


